.directorist-gutenberg-icon-picker-modal {
	.components-modal__content {
		padding: 0;
	}

	.directorist-gutenberg-icon-picker-content {
		display: flex;
		height: 600px;
		overflow: hidden;
	}

	.directorist-gutenberg-icon-picker-sidebar {
		width: 280px;
		padding: 20px;
		background: #f6f7f7;
		border-right: 1px solid #ddd;
		display: flex;
		flex-direction: column;
		gap: 16px;
		overflow-y: auto;
	}
	.directorist-gutenberg-icon-picker-search {
		margin-bottom: 0;
	}

	.directorist-gutenberg-icon-picker-filter {
		select {
			width: 100%;
			padding: 8px;
			border: 1px solid #ddd;
			border-radius: 4px;
			font-size: 13px;
		}
	}

	.directorist-gutenberg-icon-picker-info {
		p {
			margin: 0;
			font-size: 13px;
			color: #50575e;

			strong {
				font-weight: 600;
			}
		}
	}

	.directorist-gutenberg-icon-picker-preview-panel {
		padding: 16px;
		background: #fff;
		border: 1px solid #ddd;
		border-radius: 4px;
		text-align: center;

		.directorist-gutenberg-icon-picker-preview-icon {
			width: 48px;
			height: 48px;
			margin: 0 auto 12px;
			display: flex;
			align-items: center;
			justify-content: center;

			svg {
				width: 100%;
				height: 100%;
				fill: currentColor;
			}
		}

		.directorist-gutenberg-icon-picker-preview-name {
			font-size: 14px;
			font-weight: 600;
			margin-bottom: 8px;
			color: #1e1e1e;
		}

		.directorist-gutenberg-icon-picker-preview-path {
			code {
				font-size: 11px;
				color: #757575;
				background: #f0f0f1;
				padding: 4px 8px;
				border-radius: 3px;
				display: inline-block;
			}
		}
	}

	.directorist-gutenberg-icon-picker-done {
		margin-top: auto;
		padding-top: 16px;
		border-top: 1px solid #ddd;
	}

	.directorist-gutenberg-icon-picker-main {
		flex: 1;
		display: flex;
		flex-direction: column;
		overflow: hidden;
	}
	.directorist-gutenberg-icon-picker-header {
		padding: 20px;
		border-bottom: 1px solid #ddd;
		display: flex;
		justify-content: space-between;
		align-items: center;

		h2 {
			margin: 0;
			font-size: 16px;
			font-weight: 600;
		}

		.directorist-gutenberg-icon-picker-close {
			background: none;
			border: none;
			cursor: pointer;
			padding: 4px;
			display: flex;
			align-items: center;
			justify-content: center;
			color: #50575e;
			transition: color 0.2s;

			&:hover {
				color: #1e1e1e;
			}

			svg {
				width: 20px;
				height: 20px;
				fill: currentColor;
			}
		}
	}

	.directorist-gutenberg-icon-picker-icons {
		flex: 1;
		padding: 20px;
		overflow-y: auto;
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
		gap: 12px;
		place-content: start;
	}
	.directorist-gutenberg-icon-picker-icon {
		padding: 16px;
		border: 2px solid #ddd;
		border-radius: 4px;
		cursor: pointer;
		text-align: center;
		transition: all 0.2s;
		background: #fff;

		&:hover {
			border-color: #2271b1;
			background: #f0f6fc;
			transform: translateY(-2px);
			box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
		}

		&.is-selected {
			border-color: #2271b1;
			background: #f0f6fc;
		}
	}
	.directorist-gutenberg-icon-picker-icon-svg {
		width: 32px;
		height: 32px;
		margin: 0 auto 8px;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			width: 100%;
			height: 100%;
			fill: currentColor;
		}
	}

	.directorist-gutenberg-icon-picker-icon-name {
		font-size: 11px;
		color: #50575e;
		line-height: 1.3;
		word-break: break-word;
	}

	.directorist-gutenberg-icon-picker-empty {
		text-align: center;
		padding: 60px 20px;
		color: #757575;

		p {
			margin: 0;
			font-size: 14px;
		}
	}

	.directorist-gutenberg-icon-picker-loading {
		grid-column: 1 / -1;
		text-align: center;
		padding: 20px;
		color: #757575;
		font-size: 13px;
	}

	.directorist-gutenberg-icon-picker-icon-placeholder {
		width: 32px;
		height: 32px;
		background: #f0f0f1;
		border-radius: 3px;
		animation: pulse 1.5s ease-in-out infinite;
	}

	@keyframes pulse {
		0%, 100% {
			opacity: 1;
		}
		50% {
			opacity: 0.5;
		}
	}
}

